import useApiData from "../../data/useApiData";
import { getMessage } from "../../data/api";

export default function useMessage(topic: string, messageId?: string) {
  const { data, ...others } = useApiData(
    () => getMessage(topic, messageId),
    undefined,
    [topic /*    */],
  );

  return {
    message: data,
    ...others,
  };
}
